@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:muli}.navbar{background-color:#f3f2f2 !important;padding:15px;border-radius:10px}.navbar .navbar-brand{font-size:1.9rem !important;color:#565387 !important}.navbar a{font-size:1.3rem;text-transform:capitalize}.navbar .menu_active{font-weight:bold;border-bottom:1px solid #565387}.navbar a:hover{color:#542fd8 !important}@media (max-width: 991px){.navbar-brand{font-size:1.5rem !important}.menu_active{font-weight:bold;border-bottom:none}}#header{width:100%;height:80vh;margin-top:5px}#header h1{margin:0 0 10px 0;font-size:48px;font-weight:700;color:black}#header .brand-name{color:#542fd8}#header h2{color:#484848}#header .btn-get-started{font-weight:500;font-size:16px;letter-spacing:1px;display:inline-block;padding:10px 30px;border-radius:50px;transition:0.5s;color:#542fd8;border:2px solid #542fd8;text-decoration:none}#header .btn-get-started:hover{background:#542fd8;color:#fff}#header #header-img{text-align:right}#header .animated{animation:up-down 2s ease-in-out infinite alternate-reverse both}@keyframes up-down{0%{transform:translateY(10px)}100%{transform:translateY(-10px)}}.card{transition:all 0.4s cubic-bezier(0.175, 0.885, 0, 1);box-shadow:0px 13px 10px -7px rgba(0,0,0,0.3);cursor:pointer}.card:hover{box-shadow:0px 30px 18px -8px rgba(0,0,0,0.5);transform:scale(1.05, 1.05)}

